/* Tree Menu styles Starts */
.tree, .tree ul {
    margin: 0;
    padding: 0;
    list-style: none
}

    .tree ul {
        margin-left: 1em;
        position: relative
    }

        .tree ul ul {
            margin-left: .5em
        }

        .tree ul:before {
            content: "";
            display: block;
            width: 0;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            border-left: 1px solid
        }

    .tree li {
        margin: 0;
        padding: 0 1em;
        line-height: 2em;
        color: #7d8089;
        font-weight: 700;
        position: relative
    }

    .tree ul li:before {
        content: "";
        display: block;
        width: 10px;
        height: 0;
        border-top: 1px solid;
        margin-top: -1px;
        position: absolute;
        top: 1em;
        left: 0
    }

    .tree ul li:last-child:before {
        background: #fff;
        height: auto;
        top: 1em;
        bottom: 0
    }

.indicator {
    margin-right: 5px;
}

.tree li a {
    text-decoration: none;
    color: #fff;
}

.tree li button, .tree li button:active, .tree li button:focus {
    text-decoration: none;
    color: #7d8089;
    border: none;
    background: transparent;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    outline: 0;
}

.tree ul li.branch {
    font-size: 14px !important;
    color: #fff !important;
}

.tree ul li ul li {
    font-size: 13px !important;
    color: #7d8089 !important;
}
/* Tree Menu styles Ends */
/* Push Menu styles Starts */
.inner-body {
    background: #e0e8ea;
}

.pushmenu { /*this is the nav*/
    background: #0E1428 url(../images/logo-leaf.png) no-repeat -20px center;
    background-size: contain;
    text-align: left;
    width: 250px;
    height: 100%;
    top: 0;
    z-index: 1000;
    position: fixed;
}

    .pushmenu h3 {
        opacity: 0.42;
        color: #FFFFFF;
        font-size: 12px;
        font-weight: 400;
        padding: 18px 25px;
        margin: 0;
        background: #0E1428;
        height: 48px;
    }

.links {
    list-style-type: none;
    padding: 0;
    margin: 0 0 0 25%;
    width: 50%;
}

    .links li {
        margin-top: 30px;
    }

        .links li a {
            position: relative;
            display: block; /* drops the nav vertically*/
            color: #f1f1f1;
            font-size: 1.3em;
            font-weight: 400;
            text-decoration: none;
            padding: 14px;
        }

            .links li a:after {
                content: '';
                display: block;
                position: absolute;
                left: 20px;
                bottom: -5px;
                width: 0;
                height: 4px;
                background-color: #f1f1f1;
                -webkit-transition: width 0.3s ease;
                -moz-transition: width 0.3s ease;
                transition: width 0.3s ease;
            }

            .links li a:hover:after {
                width: 70%;
            }

            .links li a:active {
                color: #dbdbdb;
            }

.pushmenu-left {
    left: -250px;
}

    .pushmenu-left.pushmenu-open {
        left: 0;
    }

.pushmenu-push {
    overflow-x: hidden;
    position: relative;
    left: 0;
}

.pushmenu-push-toright {
    left: 250px;
}
/*Transition*/

.pushmenu, .pushmenu-push {
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

